.wrapper {
    box-sizing: border-box;
    margin: 0.25em;
    padding: 0;
    width: 60px;
    height: 60px;

    color: white;
    font-family: Roboto, Arial, sans-serif;
    font-weight: bold;

    background-color: #6d8f99;
    border: none;
    border-radius: 1em;
    outline: none;
}